<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 10,  //当前表格的名称编号
        setting: null
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        // this.setting = {'1_1': 10, '2_1': 5}
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
              J(this).prop('checked', true)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
