  }

  input[type=text], select {
    width: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: center;
  }

  input[type=radio],
  input[type=checkbox] {
    position: relative;
    top: 2.5px;
    cursor: pointer;
  }

  td {
    text-align: left;
    padding: 3px 2px;
  }

  .p2 {
    position: relative;
  }

  .bg-write {
    background: #ccccff;
  }

  .bg-unwrite {
    background: #d6ebff;
  }

  .tdcon {
    text-align: center;
  }
</style>



